import React from 'react'
import "./Header.scss"
import {AiOutlineSearch,AiFillHome} from "react-icons/ai"

import { Link } from 'react-router-dom'


const Header = () => {
  return (
    <div className='main'>
        <div className='left'><Link to="/"><span className='green'>G</span>reen <span className='basket'>B</span>asket </Link></div>
        <ul className='center'>
           
            <li><Link to="/service"> Service</Link></li>
            <li ><Link to="/about"> About</Link></li>
            <li><Link to="/contact"> Contact</Link></li>
        </ul>
        <div className='right'><AiOutlineSearch/> <AiFillHome/></div>
    </div>
  )
}







export default Header;