import React from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import './Slider.scss'

import Slider1 from "../../../assets/forslider/sd1.jpg";
import Slider2 from "../../../assets/forslider/slid1.jpg";
import Slider3 from "../../../assets/forslider/slide3.jpg";
// import Slider4 from "../../assets/sd.png";

const Slider = () => {
  return (
    <div className='s-main'>

    
      <div className='slider-main'>
        <Carousel
        autoFocus={true}
        autoPlay={true}
        emulateTouch={true}
        infiniteLoop={true}
        interval={3000}
        transitionTime={2000}
        showThumbs={false}
        hight={200}
       
        >
            <div>
                <img src={Slider1} alt=""/>
                <h1>slide1</h1>
                
                
            </div>
            <div>
            <img src={Slider2} alt=""/>
            <h1>slide2</h1>
               
            </div>
            <div>
            <img src={Slider3} alt="" />
                <h1>slide3</h1>
                
            </div>
            <div>
            <img src={Slider1} alt="" />
                <h1>slide4</h1>
               
            </div>
        </Carousel>
      </div>
      <div className='text'>
        <h1>TODAY OFFER</h1>
        <span>99% OFF</span>
      </div>
     
    </div>
  );
}

export default Slider