import React from 'react'
import CountUp from 'react-countup';
import {MdOutlineMapsHomeWork} from "react-icons/md"
import "./Increment.scss";


const Increments = () => {
  return (
    <div className='main-1'>   
     <h1>Our Service Available
     </h1>
      <div className='main-2'>
        
        <div className='count'>
        <div className='icon'></div>

          <div className='common'>
          <CountUp
          start={0}
          end={100}
          duration={9.75}
          separator=" "
          decimals={0}
         
        
          // prefix="EUR "
          suffix="+"
        
        />
        <h4>County</h4>
        </div>
        </div>

        <div className='count'>
              

              <div className='common'>
          <CountUp
          start={0}
          end={500}
          duration={9.75}
          separator=" "
          decimals={0}
          
        
          // prefix="EUR "
          suffix="+"
        
        />
        <h4>State</h4>
        </div>
        </div>
        <div className='count'>
            

              <div className='common'>
          <CountUp
          start={0}
          end={2000}
          duration={9.75}
          separator=""
          decimals={0}
        
          // prefix="EUR "
          suffix="+"
        
        />
        <h4>Cities</h4>
        </div>
        </div>
        
      </div>
</div> 









    )
  }

export default Increments